.contactForm{
    display: flex;
    flex-wrap: wrap;
    max-width: 300px;
    border: 2px solid black ;
    padding: 15px;
}
.labelForm{
    display: inline-block;
    margin-right: 200px;
    margin-bottom: 10px;
    font-size: 16px;
    font-weight: 500;
}
.inputForm{
    display: inline-block;
    margin-bottom: 10px;
    font-size: 16px;
    padding: 5px;
}
.buttonContactForm{
    display: block;
    margin-right: 200px;
    max-width: 300px;
}
.buttonContactForm:focus{
    background-color: #0394fc;
}
